#projects {
    text-align: center;
}

.cards-pro {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 1rem;
    box-shadow: 0 0 2rem rgba(95, 169, 255, 0.4);
    border-radius: 2rem;
    font-size: 1.5rem;
    padding: 1rem;
    background-color: var(--clr_cyber_light);
    margin-top: clamp(5rem, 1rem + 5vh, 10rem);
    margin-left: 1rem;
    margin-right: 1rem;
}

@media only screen and (min-width: 40rem) {
    .cards-pro {
        grid-template-columns: repeat(2, 1fr);
        gap: 5rem;
    }
}

@media only screen and (min-width: 80rem) {
    .cards-pro {
        grid-template-columns: repeat(2, 1fr);
        gap: 5rem;
    }
}

.card-projects {
    padding: 0 1rem;
    background-color: var(--clr_cyber_blue);
    border-radius: 2rem;
    box-shadow: 0 0 1rem rgba(0, 0, 0, 0.4);
    display: block;
    transition: ease-in 0.5s;
}

.card-projects:hover {
    box-shadow: 0 0 2rem var(--clr_cyan);
    animation: gelatine 0.8s;
}

@keyframes gelatine {
    from, to { transform: scale(1, 1); }
    25% { transform: scale(0.9, 1.1); }
    50% { transform: scale(1.1, 0.9); }
    75% { transform: scale(0.95, 1.05); }
  }

.card-projects:hover a {
    animation: pulse 1s infinite ease-in-out alternate;
}

.card-projects:hover p {
    color: var(--clr_white);
    transition: ease-in 0.5s;
}

@keyframes pulse {
    from {
        transform: scale(0.8);
    }

    to {
        transform: scale(1.2);
    }
}

.card-projects>* {
    padding: 2rem 0;
}

.card-projects h1 {
    font-size: 2rem;
}

.card-projects p {
    color: var(--clr_shade);
    transition: ease-in 0.5s;
}

.pro-btns {
    display: flex;
    justify-content: space-around;
}

.pro-btns svg {
    font-size: 4rem;
    color: var(--clr_yellow);
}