#about {
    text-align: center;
    padding-top: clamp(5rem, 1rem + 5vh, 10rem);
}

.cards-about {
    display: flex;
    flex-wrap: wrap;
    padding-top:  clamp(5rem, 1rem + 5vh, 10rem);
}

.cards-about > * {
    flex: 1 1 45rem;
    box-shadow: 0 0 1rem rgba(95, 169, 255, 0.4);    
    margin: 1rem;
    padding: 1rem;
    border-radius: 2rem;
    background-color: var(--clr_cyber_light);
    color: var(--clr_white);
    margin-bottom: 4rem;
}

.cards-about h4,
.cards-about p {
    font-size: 1.5rem;
    padding: 1rem;
}

.card-img {
    background: linear-gradient(
        45deg,
        transparent,
        var(--clr_blue_highlight),
        transparent
    );
    padding: 0;
}

.handsome {
    max-width: 100%;
    border-radius: 2rem;
    transform: rotate(10deg);
    box-shadow: 0 0 2rem rgba(95, 169, 255, 0.4); 
    transition: all ease 0.5s;
}

.handsome:hover {
    transform: rotate(0);
    box-shadow: 0 0 2rem var(--clr_cyan); 
}

.card-paths {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 1.2rem;
}

.card-paths > * {
    box-shadow: 0 0 1rem rgba(0,0,0, 0.4);
    background-color: var(--clr_cyber_blue);
    border-radius: 2rem;

    /* transition: all 1s ease; */
    transition: ease-in 0.5s;
}

.card-paths > *:hover {
    /* transition: all 1s ease; */
    box-shadow: 0 0 2rem rgb(56,251,219);
    animation: gelatine 0.8s;
}
@keyframes gelatine {
    from, to { transform: scale(1, 1); }
    25% { transform: scale(0.9, 1.1); }
    50% { transform: scale(1.1, 0.9); }
    75% { transform: scale(0.95, 1.05); }
  }

  .web,
  .cyber {
      padding: 0.5rem;
  }

  .web h4,
  .cyber h4 {
      color: var(--clr_white);
      transition: all 0.5s ease-in;
      padding-bottom: 1rem;
  }

.web:hover h4,
.cyber:hover h4 {
    color: var(--clr_yellow);
    transition: all 0.5s ease-in;
}

.card-paths svg {
    font-size: 5rem;
    margin: 1rem 0 0;
    color: var(--clr_cyan);
    text-shadow: 0 0 1rem rgba(0,0,0, 0.4);
}

.card-txt {
    padding: 5rem 2rem;
}

.card-txt div h1 {
    color: var(--clr_yellow);
    font-size: 4rem;
    padding-bottom: 1.5rem;
}
