#interests {
    text-align: center;
}
.cards-int {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 1rem;
    box-shadow: 0 0 2rem rgba(95, 169, 255, 0.4); 
    border-radius: 2rem;
    padding: 1rem;
    background-color: var(--clr_cyber_light);
    margin-top:  clamp(5rem, 1rem + 5vh, 10rem);
    margin-left: 1rem;
    margin-right: 1rem;
}

.card-interests {
    padding: 1rem 1rem 2rem 1rem;
    background-color: var(--clr_cyber_blue);
    border-radius: 2rem;
    box-shadow: 0 0 1rem rgba(0,0,0, 0.4);
    display: block;
    transition: ease-in 0.5s;
}

.card-interests:hover {
    box-shadow: 0 0 2rem rgba(56,251,219);
    animation: gelatine 0.8s;
  }
  @keyframes gelatine {
    from, to { transform: scale(1, 1); }
    25% { transform: scale(0.9, 1.1); }
    50% { transform: scale(1.1, 0.9); }
    75% { transform: scale(0.95, 1.05); }
  }

.card-interests:hover p {
    color: var(--clr_yellow);
    transition: all 0.5s ease-in;
}

@media only screen and (min-width: 80rem) {
    .cards-int {
        grid-template-columns: repeat(3, 1fr);
        gap: 5rem;
    }
}

.cards-int svg {
    color: var(--clr_cyan);
    margin-bottom: 0.5rem;
    font-size: 5rem;
}

.cards-int h5 {
    font-family: var(--font_headings);
    margin-bottom: 0.5rem;
}

.cards-int p {
    color: var(--clr_shade);
    font-size:  1.5rem;
    transition: ease-in 0.5s;
}