.nav {
    background: rgba(18,44,90, 0.2);
    width: max-content;
    display: block;
    padding: 0.8rem 1.6rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    bottom: 2rem;
    transform: translateX(-50%);
    display: flex;
    gap: 1rem;
    border-radius: 10rem;
    backdrop-filter: blur(1.5rem);
}

.nav-item {
    background: transparent;
    color: var(--clr_white);
    padding: 0.8rem;
    display: flex;
    border-radius: 50%;
    font-size: 2.2rem;
}

.nav-item:hover {
    background-color: rgba(18, 44, 90, 0.8);
    color: var(--clr_blue_highlight);
    transition: all 0.5s ease;
    -webkit-transition: all 0.5s ease;
}

.nav-item.active {
    background-color: rgba(18, 44, 90, 0.8);
    color: var(--clr_cyan);
}
