@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@500;700&display=swap');



/* ============= CONSTANT VARIABLES ============= */

:root {
    /* Fonts */
    --font_headings: 'Orbitron', sans-serif;
    --font_txt: 'Ubuntu', sans-serif;

    /* Colors */
    --clr_cyber_blue: #0c1e3e; /* #0E4A92 #0c1e3e #122c5a */
    --clr_cyber_light: #122c5a;
    --clr_blue_highlight: #5FA9FF;
    --clr_shade: #A7AABD;
    --clr_cyan: #38fbdb;
    --clr_yellow: #F2BF3A; /* #D4A418 #F2BF3A */
    --clr_white: #E7EFFF;
}


/* =============== GENERAL STYLES =============== */

/* Initial formatting to all elements */
* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

/* Smooth scrolling */
html {
    /* scroll-behavior: smooth; */
    font-size: 62.5%;
}

/* Hide scrollbar */
::-webkit-scrollbar {
    display: none;
}

body {
    font-family: var(--font_txt);
    background-color: var(--clr_cyber_blue);
    color: var(--clr_white);
}

/* Formatting sections about, experience, projects, interests etc */
section {
    padding: 0 1rem;
    padding-top: 5rem;
    padding-bottom: 2.5rem;
    overflow: hidden;
    min-height: 85vh;
}

section h1{
    font-size: clamp(2rem, 1rem + 2vh, 8rem);
}

section h5{
    font-size: clamp(1rem, 1rem + 0.5vh, 4rem);
}

section p {
    line-height: 2rem;
}

.section-main-header {
    margin-top: 0.5rem;
    padding: 0.5rem;
    /* background-color: var(--clr_cyber_blue); */
    color: var(--clr_white);
    /* border-radius: 10rem; */
}

.section-sub-header {
    color: var(--clr_shade);
}

/* Heading formatting */
h1, h2, h3, h4, h5 {
    font-family: var(--font_headings);
}

/* Highlighted span element */
.highlight-txt {
    color: var(--clr_yellow);
    text-shadow: 0 0 0.2rem var(--clr_yellow);
}
