.header-container {
    background-color: var(--clr_cyber_blue);
    color: var(--clr_white);
    padding-top: 33vh;
    padding-bottom: 33vh;
    text-align: center;
    min-height: 100vh;
}

.header-container h1 {
    font-size: clamp(3rem, 1rem + 4vh, 10rem);
    padding: 0.8rem;
}

.header-container h5 {
    font-size: clamp(1.5rem, 1rem + 2vh, 5rem);
}

.header-container p {
    color: var(--clr_shade);
    font-weight: 300;
}

.headerLinks-container {
    display: flex;
    justify-content: space-around;
    padding-top: 4rem;
}

.link-item {
    color: var(--clr_yellow);
    font-size: 3.5rem;
    border: solid 0.2rem var(--clr_yellow);
    border-radius: 50%;
    padding: 1rem;
    display: flex;
    box-shadow: 0 0 0.4rem var(--clr_yellow);
    transition-timing-function: ease-in-out;
    transition: 0.25s;
}

/* .link-item:hover {
    box-shadow: 0 0 0.8rem var(--clr_yellow);
    text-shadow: 0 0 0.8rem var(--clr_yellow);
    transform: scale(1.5);
} */

.link-item:hover {
	backface-visibility: visible !important;
	animation: flip 2s ease;
}
@keyframes flip {
	0% {
		transform: perspective(40rem) rotateY(0);
		animation-timing-function: ease-out;
	}
	40% {
		transform: perspective(40rem) translateZ(15rem) rotateY(170deg);
		animation-timing-function: ease-out;
	}
	50% {
		transform: perspective(40rem) translateZ(15rem) rotateY(190deg) scale(1);
		animation-timing-function: ease-in;
	}
	80% {
		transform: perspective(40rem) rotateY(360deg) scale(.95);
		animation-timing-function: ease-in;
	}
	100% {
		transform: perspective(40rem) scale(1);
		animation-timing-function: ease-in;
	}
}

